.front{
      position: absolute;
      width: 100%;
      height: auto;
      top: 40px;
      left: 0;
      z-index: 2;
}

.front_user{
      position: absolute;
      top: 135px;
      left: 290px;
      width: 275px;
      height: 360px;
}

.user_banner{
      width: 275px;
      height: 360px;
      box-shadow: 2rem 2rem 20px rgba(0,0,0,.5);
      position: relative;
}

.user_banner__img{
      width: 100%;
      height: 100%;
      border-radius: .6rem;
      overflow: hidden;
      background: var(--grys-200);
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_banner__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.online_status{
      position: absolute;
      width: 100px;
      height: 32px;
      border-radius: 32px;
      background: var(--theme-400);
      top: 2rem;
      right: -3rem;
      padding: .5rem;
      display: flex;
      align-items: center;
      gap: .5rem;
}

.dots{
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: var(--grys-100);
      margin: 3px;
      box-shadow: 0 0 0 3px rgba(0,0,0,.4);
}

.dots.red{
      background: var(--red);
}

.dots.green{
      background: var(--green);
}

.online_status span{
      text-transform: uppercase;
      color: var(--light);
      font-family: var(--ft-1);
      font-weight: 500;
      font-size: 1rem;
}

.user_data{
      position: absolute;
      width: 205px;
      height: 210px;
      top: 90px;
      left: 0;
      transform: translateX(-180px);
      border-radius: 6px;
      background: var(--grys-100);
      box-shadow: 1rem 1rem 15px rgba(0,0,0,.3);
      padding: 1.5rem;
}

.user_data__header{
      position: absolute;
      width: 200px;
      height: 72px;
      top: -36px;
      left: 2rem;
      border-radius: 72px;
      display: flex;
      align-items: center;
      background: var(--theme-400);
      padding: 8px;
      gap: 1rem;
}

.data_header__avatar{
      width: 48px;
      height: 48px;
      border-radius: 50%;
      margin: 4px;
      box-shadow: 0 0 0 4px rgba(255,255,255,.1);
      overflow: hidden;
      flex-shrink: 0;
}

.data_header_info{
      flex-grow: 1;
}

.data_header_info h2{
      color: var(--light);
      font-size: 1.8rem;
      font-family: var(--ft-1);
      font-weight: 500;
}

.data_header_info span{
      padding: .3rem 1rem;
      border-radius: 999px;
      background: rgba(0,0,0,.2);
      color: var(--light);
}

.user_data__about{
      width: 100%;
      height: 140px;
      margin-top: 3rem;
      overflow: hidden;
      font-family: var(--ft-1);
}

.user_data__about p{
      color: var(--theme-400);
      font-size: 1.2rem;
}

.front_relation{
      width: 340px;
      height: 120px;
      position: absolute;
      top: 508px;
      left: 370px;
      background: var(--light);
      border-radius: 6px;
      display: flex;
      justify-content: space-between;
      gap: .5rem;
      border-left: solid 2rem var(--theme-400);
      box-shadow: 1rem 1rem 15px rgba(0,0,0,.4);
}

.relation_icon{
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: var(--theme-400);
      position: absolute;
      top: -20px;
      left: 60px;
      box-shadow: 0 0 0 5px rgba(0,0,0,.2);
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
}

.relation_icon *{
      font-size: 2.4rem;
}

.relation_data{
      flex-grow: 1;
      width: calc(100% - 94px);
      height: 100%;
      padding: .6rem;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
}

.relation_data h2{
      background: var(--theme-400);
      color: var(--light);
      padding: .5rem 1rem;
      font-size: 1.2rem;
      border-radius: 999px 0 0 999px;
      position: relative;
      transform: translateX(1.1rem);
      font-family: var(--ft-1);
}

.relation_data span{
      color: var(--grys-300);
      margin-top: .3rem;
}

.relation_about{
      width: 100%;
      height: 60px;
      color: var(--grys-300);
      font-family: var(--ft-1);
      overflow-y: auto;
      overflow-x: hidden;
      scrollbar-width: thin;
      scrollbar-color: var(--theme-200) var(--theme-500);
      margin-top: .5rem;
}

.relation_about p{
      font-size: 1.2rem;
}

.relation_avatar,
.relation_avatar a{
      width: 94px;
      height: 100%;
      flex-shrink: 0;
      border-radius: 0 6px 6px 0;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--theme-400);
}

.relation_avatar img{
      width: auto;
      min-width: 105%;
      height: auto;
      min-height: 100%;
}

.front_navigator{
      width: 84px;
      height: 120px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1rem;
      position: absolute;
      right: 0;
      top: 250px;
}

.front_navigator button{
      width: 80px;
      height: 30px;
      border-radius: 30px;
      text-transform: uppercase;
      background: transparent;
      color: var(--grys-300);
      position: relative;
      transition: .2s;
}

.front_navigator button:hover,
.front_navigator button.active{
      background: var(--black);
      transform: translateX(-30px);
      color: var(--light);
      box-shadow: 5px 5px 10px rgba(0,0,0,.4);
}