.title_back{
      position: absolute;
      top: 5rem;
      left: 2.5rem;
      z-index: 10;
}

.title_back h1{
      color: rgba(0,0,0,.21);
      font-size: 4.4rem;
      font-family: var(--ft-2);
}

.title_back p{
      color: var(--grys-200);
      font-size: 1.4rem;
}

.back_col{
      width: 50%;
      height: 100%;
      flex-shrink: 0;
      position: relative;
}

.back_left__footer{
      position: absolute;
      width: 100%;
      height: auto;
      left: 0;
      bottom: 0;
      border-bottom: solid 40px var(--theme-400);
      border-radius: 0 0 0 1rem;
      padding: 2rem;
}

.location{
      display: flex;
      align-items: center;
      gap: .5rem;
}

.location *{
      font-size: 2rem;
      color: var(--theme-500);
}

.location span{
      color: var(--grys-200);
      font-size: 1.4rem;
}

.social_media{
      margin: 2rem 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
}

.social_media a{
      width: 36px;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: var(--grys-100);
}

.social_media a *{
      color: var(--theme-500);
      font-size: 1.8rem;
}

.banner{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.banner img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}