  --grys-100: #f0f0f0;
  --green: #2fc488;
  --red: #d90000;

  /** default theme */
  --theme-500: #5f367a;
  --theme-400: #635370;
  --theme-300: #837392;
  --theme-200: #aea6b5;
  --theme-100: #d3d0d7;

