*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100;
  min-height: 100vh;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  cursor: pointer;
  border: none;
  outline: none;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--theme-200);
}

::-webkit-scrollbar-thumb{
  background: var(--theme-500);
}