.video_content__box{
      width: 100%;
      height: 450px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
}

.video_box{
      width: 400px;
      height: 271px;
      background: var(--dark);
      border-bottom: solid 5px var(--theme-300);
      box-shadow: 2rem 2rem 30px rgba(0,0,0,.4);
      position: relative;
}

.video_box iframe{
      border: none;
}