.content{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
}

.main_content{
      width: 746px;
      height: 710px;
      background: var(--dark);
      border-radius: 1rem;
      position: relative;
      box-shadow: 0 0 120px rgba(0,0,0,.5);
      overflow: hidden;
}

.main_content:before{
      width: 100%;
      height: 40px;
      background: var(--theme-400);
      top: 0;
      left: 0;
      border-radius: 1rem 1rem 0 0;
}

.back{
      width: 662px;
      height: calc(100% - 40px);
      background: var(--light);
      position: absolute;
      top: 40px;
      left: 0;
      border-radius: 0 0 0 1rem;
      display: flex;
}

.friends{
      width: 331px;
      height: 635px;
      background: var(--dark);
      z-index: 100;
      position: absolute;
      top: 40px;
      left: 0;
      transform: translateX(-100%);
}

.friends.active{
      animation: show_friends .5s ease-in-out forwards;
}

@keyframes show_friends{
      100%{
            transform: translateX(0);
      }
}

.friends_content__header{
      width: 100%;
      height: 40px;
      background: var(--theme-300);
      display: flex;
      align-items: center;
      justify-content: space-between;
}

.friends_content__header h3{
      display: flex;
      align-items: center;
      text-transform: uppercase;
      color: var(--dark);
      font-family: var(--ft-1);
      font-size: 1.2rem;
      font-weight: 400;
      gap: .5rem;
}

.friends_content__header h3 span,
.friends_content__header button{
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--light);
      color: var(--dark);
      font-size: 1.6rem;
}

.empty{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--grys-300);
      font-family: var(--ft-1);
}

/**
* PAGE MODAL INNER
*/

.page_content{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.5);
      z-index: 300;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
}

.page_content.active{
      animation: show_page .3s ease-in-out forwards;
}

@keyframes show_page{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.inner_page{
      width: 662px;
      height: 544px;
      background: rgba(255,255,255,1);
      -webkit-backdrop-filter: blur(15px);
      -moz-backdrop-filter: blur(15px);
      backdrop-filter: blur(15px);
      position: relative;
      opacity: 0;
      visibility: hidden;
      transform: scale(0);
      border-radius: .3rem;
}

.inner_page.active{
      animation: show_page_inner .5s .2s ease-in-out forwards;
}

@keyframes show_page_inner{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
      }
      70%{
            opacity: 1;
            visibility: visible;
            transform: scale(1.4);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
      }
}

.close_page{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 1rem;
      right: 1rem;
      border-radius: 50%;
      background: rgba(0,0,0,.05);
}

.page_header{
      width: 100%;
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: center;
}

.page_header h3{
      font-weight: 200;
      font-size: 2.6rem;
      color: var(--dark);
      text-align: center;
}

.modal{
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.8);
      z-index: 1000;
      opacity: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      visibility: visible;
}

.modal.show{
      animation: show_modal .3s ease-in-out forwards;
}

@keyframes show_modal{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.btn_close__modal{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 2rem;
      right: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--light);
      border-radius: 50%;
}

.btn_close__modal *{
      font-size: 1.2rem;
      color: var(--light);
      fill: var(--light);
}

.inner_modal{
      width: 90%;
      height: 90vh;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      transform: scale(0);
}

.inner_modal.show{
      animation: show_modal_inner .5s .2s ease-in-out forwards;
}

@keyframes show_modal_inner{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
      }
      70%{
            opacity: 1;
            visibility: visible;
            transform: scale(1.4);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
      }
}

.inner_modal img{
      width: initial;
      max-width: 100%;
      height: auto;
      max-height: 100%;
}

.empty.page{
      height: 320px;
}

.empty.page h3{
      font-size: 2rem;
      color: var(--grys-200);
}