.gallery_slider{
      width: 100%;
      height: 450px;
      position: relative;
      padding: 0 1rem;
}

.content_slider{
      margin-top: 2rem;
}

.card_gallery,
.card_gallery__img{
      width: 290px;
      height: 358px;
      cursor: pointer;
}

.card_gallery__img{
      border-radius: .3rem;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.card_gallery__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.slick-slide{
      margin: 0 15px;
}

.slick-list{
      margin: 0 15px;
}

.slick-slider{
      position: relative;
}

.slick-arrow{
      width: 36px;
      height: 36px;
      position: absolute;
      top: calc(50% + 30px);
      border-radius: 50%;
      z-index: 999;
}

.slick-arrow:before{
      width: 10px;
      height: 10px;
      left: 50%;
      bottom: 15%;
      color: transparent;
      border-top: solid 2px var(--theme-200);
      border-left: solid 2px var(--theme-200);
}

.slick-arrow.slick-prev:before{
      transform: translate(-50%, -50%) rotate(-45deg);
}

.slick-arrow.slick-next:before{
      transform: translate(-50%, -50%) rotate(135deg);
}


.slick-arrow.slick-prev{
      left: calc(50% - 40px);
}

.slick-arrow.slick-next{
      right: calc(50% - 40px);
}
