.friends_group,
.empty.friend{
      width: 100%;
      height: 595px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--theme-400) var(--theme-300);
}

.friends_group::-webkit-scrollbar{
      width: 8px;
      background: var(--theme-400);
}

.friends_group::-webkit-scrollbar-thumb{
      background: var(--theme-300);
}

.friends_col{
      width: calc(100% - 2px);
      height: auto;
}

.friends_row{
      width: 100%;
      height: 80px;
      display: flex;
      align-items: center;
      padding-right: .5rem;
      background: rgba(0,0,0,.3);
      margin-bottom: .2rem;
      gap: .5rem;
}

.friends_row:hover{
      background: var(--light);
}

.friends_row img{
      width: 80px;
      height: 80px;
      flex-shrink: 0;
}

.friend_data{
      flex-grow: 1;
      font-family: var(--ft-1);
      color: var(--light);
}

.friend_data span{
      font-size: 1.6rem;
      font-weight: bold;
}

.friends_row:hover .friend_data span{
      color: var(--theme-400)
}

.friend_data p{
      color: var(--grys-300);
      font-size: 1.2rem;
      margin-top: .5rem;
}

.friends_row:hover .friend_data p{
      color: var(--grys-200)
}

.friends_row a{
      padding: .4rem 1rem;
      text-transform: uppercase;
      background: var(--theme-300);
      font-family: var(--ft-1);
      color: var(--light);
      border-radius: 999px;
      flex-shrink: 0;
}